label {
  font-size: 14px;
  line-height: calc(18 /14);
  cursor: pointer; }

[type="checkbox"] {
  cursor: pointer; }

[type="checkbox"]:checked,
[type="checkbox"]:not(:checked) {
  position: absolute;
  left: -9999px; }

[type="checkbox"]:checked + label,
[type="checkbox"]:not(:checked) + label {
  display: inline-block;
  vertical-align: center;
  position: relative;
  padding-left: 25px;
  line-height: 31px;
  cursor: pointer; }

[type="checkbox"]:checked + label::before,
[type="checkbox"]:not(:checked) + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 8px;
  width: 15px;
  height: 15px;
  border: 1px solid #a87b62;
  background-color: transparent; }

[type="checkbox"]:checked:hover + label::before,
[type="checkbox"]:not(:checked):hover + label::before,
[type="checkbox"]:checked:focus + label::before,
[type="checkbox"]:not(:checked):focus + label::before {
  box-shadow: 0 0 0 1px #a87b62;
  transition: all 0.2s ease; }

[type="checkbox"]:checked:active + label,
[type="checkbox"]:not(:checked):active + label {
  opacity: 0.3;
  transition: none; }

[type="checkbox"]:checked + label::after,
[type="checkbox"]:not(:checked) + label::after {
  content: "";
  position: absolute;
  transition: all 0.2s ease;
  left: 0;
  top: 8px;
  width: 15px;
  height: 15px;
  background-color: #a87b62; }

[type="checkbox"]:not(:checked) + label::after {
  opacity: 0; }

[type="checkbox"]:checked + label::after {
  opacity: 1; }

[type="checkbox"]:disabled + label {
  color: rgba(155, 153, 151, 0.5);
  cursor: default; }

[type="checkbox"]:disabled + label::before,
[type="checkbox"]:disabled:hover + label::before,
[type="checkbox"]:disabled:active + label::before {
  border: 1px solid rgba(155, 153, 151, 0.5);
  box-shadow: none;
  cursor: default; }

